@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

:root{
  --brand-color: #FBD062;
  --font-rong: #111430;
  --article-color: #707070;
}

a:hover{
  text-decoration: none !important;
}

.brand-color{
  color: #FBD062 !important;
}

.font-rong{
  color: black !important;
}

.article-text{
  color: #707070 !important;
}

.main-btn{
  background-color: #111430;
  color: white !important;
  padding: 10px 50px;
  border-radius: 5px;
  border: none;
}

.logo{
  width: 150px;
}

li{
  list-style: none;
}

.header{
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-53%);
    z-index: 999;
}

.nav-link{
    font-weight: 500;
}

.nav-link:hover{
    font-weight: 900;
    border-bottom: 3px solid red;
}

.main-btn a{
    color: white;
}
.banner {
    padding-top: 130px;
    padding-bottom: 100px;
    position: relative;
}

.banner::before{
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--brand-color);
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 83%, 0 100%);
            clip-path: polygon(0 0, 100% 0, 100% 83%, 0 100%);
    z-index: -1;
}

.banner-heading{
    font-weight: 700;
    font-size: 50px;
}

.banner-article{
    width: 370px;
}
.brands{
    margin-top: 90px;
}

.brands-image{
    width: 150px;
}
.service{
    border-radius: 10px;
    transition: all linear 0.1s;
}

.service:hover{
    box-shadow: 2px 2px 10px lightgrey;
    cursor: pointer;
}

.service-img{
    width: 74px;
    height: 74px;
}

.service-title{
    font-size: 20px;
    font-weight: 600;
}

.service-description{
    font-size: 16px;
    font-weight: 300;
    width: 265px;
    line-height: 28px;
    margin: 0 auto;
}
.services{
    margin-top: 150px;
}


.heading{
    text-align: center;
    font-size: 34px;
    font-weight: 600;
}

.unique {
    color: #7ab259;
}
#portfolio{
    margin-top: 100px;
    padding: 70px 0 90px;
    background-color: var(green);
}

.portfolio-img{
    width: 465px;
    height: 334px;
}

.slick-slide{
    border: none !important;
}

.portfolio-slider{
    margin: 80px auto 50px !important;
}

.slick-dots li{
    width: 15px !important;
    height: 15px !important;
    border-radius: 50%;
    background-color: #242A65;
    position: relative;
    bottom: -70px !important;
}

.slick-dots li button::before{
    display: none !important;
}

.slick-dots li.slick-active{
    width: 15px !important;
    height: 15px !important;
    border-radius: 50%;
    background-color: #FBD062;
}
.reviews{
    margin-top: 100px;
}
.review{
    padding: 20px;
    border: 2px solid lightgrey;
    border-radius: 5px;
}

.review-img{
    width: 64px;
    height: 64px;
    border-radius: 50%;
    margin-right: 10px;
}

.review-clientName{
    font-size: 20px;
    font-weight: 600;
}

.review-designation{
    font-weight: 500;
    font-size: 16px;
}

.review-comment{
    line-height: 28px;
    font-size: 15px;
    font-weight: 400;
}
#contact {
    margin-top: 140px;
    padding: 55px 0 33px;
    background-color: var(--brand-color);
}

.contact-input{
    height: 60px !important;
    border-radius: 5px;
}

.contact-message{
    height: 210px !important;
    border-radius: 5px;
    resize: none !important;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder{
    padding-left: 8px;
    padding-top: 8px;
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder{
    padding-left: 8px;
    padding-top: 8px;
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder{
    padding-left: 8px;
    padding-top: 8px;
}

input::placeholder, textarea::placeholder{
    padding-left: 8px;
    padding-top: 8px;
}
.login{
    width: 570px;
    height: 450px;
    border: 1px solid #ABABAB;
    border-radius: 4px;
    margin: auto;
}

.login-title{
    font-size: 24px;
    font-weight: 700;
    text-align: center;
    margin-top: 150px;
}

.login-btn{
    padding: 12px 120px; 
    background-color: #fff;
    border: 1px solid lightgrey;
    border-radius: 30px;
    position: relative;
}

.google-icon{
    position: absolute;
    top: 3px;
    left: 3px;
    width: 10%;
}

.login-unique{
    color: #3F90FC;
    cursor: pointer;
}

.login-unique:hover{
    text-decoration: underline;
}
.notFound{
    color: red;
    text-align: center;
}
.success{
    text-align: center;
    color: green;
}
